import { useState } from "react";
import { ApiDocService } from "../../../../../services/taskService";
import { Box } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import JsonViewerComponent from "../../../test/JsonViewerComponent";
import "../../task.css";

export default function ExecuteApiComponent(props: any) {
    const { projectKey, folderKey, apiKey } = props;
    const [statusCode, setStatusCode] = useState('');
    const [response, setResponse] = useState({});

    const { isLoading, setIsLoading } = props;

    function OnClickExec() {
        setIsLoading(true);
        ApiDocService.ExecApi(projectKey, folderKey, apiKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    console.log(rs.data);
                    setStatusCode(rs.data.result.statusCode);
                    setResponse(rs.data.result.data);
                }
            })
            .finally(() => setIsLoading(false));
    }

    return (
        <Box>
            <MyButton
                isLoading={isLoading}
                value="Chạy APi"
                type="warning"
                onClick={OnClickExec}
            />
            <p>Status code: {statusCode}</p>
            <pre>
                <JsonViewerComponent data={response} />
            </pre>
        </Box>
    );
}