import { Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { ApiDocService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyBox } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import ExecuteApiComponent from "./ExecuteApiComponent";
import HeaderComponent from "./HeaderComponent";

export default function ModifyApiDocComponent(props: any) {
    const key = useParams().key;
    const projectKey = useParams().projectKey;
    const folderKey = useParams().folderKey;
    const navigate = useNavigate();
    const [headers, setHeaders] = useState([]);
    const [name, setName] = useState('');
    const [method, setMethod] = useState(1);
    const [url, setUrl] = useState('');
    const [body, setBody] = useState<string>('');
    const [varReplace, setVarReplace] = useState();
    const [isLoading, setIsLoading] = useState(false);

    const options = [
        {
            value: 1,
            label: "Get"
        },
        {
            value: 2,
            label: "Post"
        },
        {
            value: 3,
            label: "Put"
        },
        {
            value: 4,
            label: "Delete"
        }
    ]

    const [bodyObj, setBodyObj] = useState();

    function isJSON(str: string) {
        try {
            return (JSON.parse(str) && !!str);
        } catch (e) {
            return false;
        }
    }

    const loadData = useCallback(() => {
        if (key !== undefined) {
            setIsLoading(true);
            ApiDocService.GetDetail(projectKey, folderKey, key)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        let row = rs.data;
                        setHeaders(row.headers);
                        setName(row.name);
                        setMethod(row.method);
                        setUrl(row.url);
                        setVarReplace(row.varReplace);
                        setBody(row.body === null ? "" : JSON.stringify(row.body));
                        setBodyObj(row.body);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [projectKey, folderKey, key, setIsLoading])

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleOnChangeBody(value: string) {
        if (isJSON(value)) {
            setBodyObj(JSON.parse(value));
        }
        setBody(value);
    }

    function filterArray() {
        var t = headers;

        t = t.filter((obj: any) => {
            return (obj !== undefined && obj.key !== '');
        })

        return t;
    }

    function handleSubmitForm() {
        setIsLoading(true);
        let bodyReq = {
            name: name,
            headers: filterArray(),
            url: url,
            method: method,
            varReplace: varReplace,
            body: bodyObj
        };

        if (key === undefined) {
            add(bodyReq);
        } else {
            edit(bodyReq);
        }
    }

    function add(bodyReq: any) {
        ApiDocService.Add(projectKey, folderKey, bodyReq)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    navigate(`/tools/tasks/projects/${projectKey}/codeManage/document/${folderKey}/apis/${rs.data.key}`);
                }
            })
    }

    function edit(bodyReq: any) {
        ApiDocService.GetDetail(projectKey, folderKey, key)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    bodyReq = Fn.GetTimestampV2(rs, bodyReq);
                    ApiDocService.Edit(projectKey, folderKey, key, bodyReq)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                            }
                        })
                        .finally(() => setIsLoading(false));
                } else setIsLoading(false);
            })
    }

    function handleDelete() {
        Swal.fire("Xác nhận xóa", "Bạn có muốn xóa dữ liệu không?", "question")
            .then((result) => {
                if (result.isConfirmed) {
                    doDelete();
                }
            });
    }

    function doDelete() {
        setIsLoading(true);
        ApiDocService.GetDetail(projectKey, folderKey, key)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    ApiDocService.Delete(projectKey, folderKey, key, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                navigate(`/tools/tasks/projects/${projectKey}/codeManage/document?folder=${folderKey}`);
                            }
                        })
                }
            })
    }

    return (
        <MyBox
            title={key !== undefined ? "Chi tiết API" : "Thêm mới API"}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Select
                    label="Phương thức"
                    options={options}
                    value={method}
                    setValue={setMethod} />

                <MyFormItem.Input
                    label="URL"
                    value={url}
                    setValue={setUrl} />

                <HeaderComponent
                    data={headers}
                    setData={setHeaders} />

                <MyFormItem.TextArea
                    label="Body"
                    value={body}
                    setValue={(value: string) => {
                        handleOnChangeBody(value);
                    }} />

                <MyFormItem.TextArea
                    label="Script lưu biến "
                    value={varReplace}
                    setValue={setVarReplace} />

                <MyButton
                    htmlType="submit"
                    isLoading={isLoading}
                    type="success"
                    value={key === undefined ? "Thêm mới" : "Chỉnh sửa"} />

                <MyButton
                    type="danger"
                    isLoading={isLoading}
                    onClick={handleDelete}
                    disabled={key === undefined}
                    value="Xóa" />
            </Form>

            <ExecuteApiComponent
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                projectKey={projectKey}
                folderKey={folderKey}
                apiKey={key} />
        </MyBox>
    )
}